html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

html {
    width: 100%;
    height: 100%;
    background-color: #000;
}

body {
    height: 100%;
    margin: 0;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    font-family: sans-serif;
}

canvas {
    max-width: 100%;
    max-height: 100%;
    margin: auto;
    transform-origin: top center;
}
